.top-bar-container {
  // position: absolute;
  width: 100vw;
  height: 8vmin;
  min-height: 40px;
  
  background: black;
  // background: #00526c;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  & > .right-halve {
    display: flex;
    justify-content: space-evenly;
    // background: black;
    width: 60%;

    & > button {
      color: white;
      height: 7vmin;
      min-height: 38px;
      margin: 0px, 5px, 0px, 5px;
    }

    & > button:hover {
      background: rgba(189, 191, 194, 0.5);
    }
  }

  @media only screen and (max-width: 600px) {
    .left-halve {
      visibility: hidden;
    }
    .right-halve {
      width: 100%;
    }
  }
}

.top-bar-container:hover {
    // height: 0;
}
